import { ajaxPro } from "req/http/http";

export function addModule(param) {
  return ajaxPro({
    url: `host/notadded/${param.module}/add`,
    type: "post",
    data: param
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function delModule(param) {
  return ajaxPro({
    url: `host/notadded/${param.module}/delete`,
    type: "post",
    data: param
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}



