<template>
  <div class="hostservice-addModule">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      @tableRespData="tableRespData"
      ref="hostservice_addModule_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" :disabled="respData.remianing_number == 0" @click="addHost(selectData)"
          >批量添加主机</el-button
        >
        <span class="addModule_remianing_number">剩余授权数  <mu-badge :content="respData.remianing_number ? respData.remianing_number.toString() : '0'" color="secondary"></mu-badge></span>
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button type="primary" size="mini" @click="edit(props.data.row)"
          >删除</el-button
        >
      </template>
      <!-- 资产名称 -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          :maxLength="20"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
        ></el-input>
      </template>
      <!-- IP地址 -->
      <template v-slot:ip_head="props">
        <el-input
          v-model="params.ip"
          :maxLength="20"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
        ></el-input>
      </template>
      <!-- 状态 -->
      <template slot="online" slot-scope="props">
        <div v-if="props.data.row.online == 1">
          <span v-html="icon.linkIcon.link"></span>在线
        </div>
        <div v-else-if="props.data.row.online == 0">
          <span v-html="icon.linkIcon.unlink"></span>离线
        </div>
      </template>
      <template v-slot:online_head="props">
        <el-select
          v-model="params.online"
          @change="search"
          clearable
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.online"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import { addModule } from "req/api/hostservice/addModule";
export default {
  data() {
    return {
      url: `host/notadded/${this.moduleType}/list`,
      type: "post",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
         {
          label: "资产名称",
          prop: "name",
          sortable: true,
          second_head: {
            minWidth: "200px",
            templateHeadName: "name_head",
            prop: "name"
          }
        },
        {
          label: "IP地址",
          sortable: true,
          prop: "ip",
          sortMethod: this.$tools.IPSort,
          second_head: {
            prop: "ip",
            minWidth: "200px",
            templateHeadName: "ip_head"
          }
        },
        {
          label: "状态",
          sortable: true,
          prop: "online",
          second_head: {
            isTemplate:true,
            templateName:"online",
            prop: "online",
            minWidth: "150px",
            templateHeadName: "online_head"
          }
        }
        // {
        //   label: "操作",
        //   second_head: {
        //     label: "",
        //     minWidth: "100px",
        //     isTemplate: true,
        //     templateName: "operation"
        //   }
        // }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        ip: "",
        name: "",
        online:"",
        module: this.moduleType
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        online: [
          {
            id: 1,
            name: "在线"
          },
          {
            id: 0,
            name: "离线"
          }
        ]
      },
      icon: {
        linkIcon: this.$icons.linkIcon
      },
      selectData: [],
      respData: {}
    };
  },
  props: {
    layerid: String,
    moduleType: String,
    tipText: String
  },
  methods: {
    reload() {
      this.$refs.hostservice_addModule_table.reload();
    },
    search() {
      this.params.page = 1;
      this.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    tableRespData(data) {
      this.respData = data
    },
    addHost(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择需要添加的主机!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        this.tipText,
        "确定添加吗?",
        () => {
          this.__addModule({ host_id: ids, module: this.moduleType });
        },
        this
      );
    },
    __addModule(param) {
      addModule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "添加成功"
          });
          this.$parent.reload();
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less" scoped>
.hostservice-addModule {
  width: 100%;
  padding: 10px;
  .addModule_remianing_number {
    float:right;
    color:#f56c6c;
  }
}
</style>



